import { CommerceEntityInput } from '../commerce-entity-input';
describe('commerce entity input', () => {
    describe('shouldCacheOutput', () => {
        it('Should respect commerce entity input set to true', () => {
            const input = new CommerceEntityInput({
                shouldCacheOutput: true,
                cacheKey: 'mock',
                cacheObjectType: 'mock'
            });
            expect(input.shouldCacheOutput()).toBe(true);
        });
        it('Should respect commerce entity input set to false', () => {
            const input = new CommerceEntityInput({
                shouldCacheOutput: false,
                cacheKey: 'mock',
                cacheObjectType: 'mock'
            });
            expect(input.shouldCacheOutput()).toBe(false);
        });
    });
});
